import React from "react";
import "./Batch.css";
function BatchCard(props) {
  return (
    <div className="batch-card">
      <div className="box card py-5 px-4 shadow">
        <h3 style={{height:"3rem"}} className="text-center mb-1 "> {props.batch}</h3>
        <ol className="text-start ps-0 text-capitalize">
        <li>{props.size}</li>
        <li>{props.attention}</li>
        <li>{props.schedule}</li>
        <li>{props.learning}</li>
        <li>{props.record}</li>
        <li>{props.cover}</li>
        <li >{props.skill}</li>
        </ol>
      </div>
    </div>
  );
}

export default BatchCard;
