import React from 'react'
import "./OurTrainer.css";
import img from "../../All_image/home-image.jpg";
import { trainerdata } from '../../Data/CourseData';
function OurTraner() {
  return (
    <div className=' trainer'>
        <div className="container-fluid">
            <div className="row">
                <div className="col-11 m-auto p-4 ">
                <h1 className='text-center'>Meet our IIt and IB-trained curriculum creators</h1>
                <div className="row animate">
              {trainerdata.map((data,key)=>(  <div className="col image-container text-center mt-5">
                    <img src={img} style={{width:"200px"}} className="rounded-circle " alt="" />
                    <h5 className='text-uppercase '>{data.name}</h5>
                    <p className=' text-uppercase'>{data.deegree}</p>
                </div>
                ))}
                </div>
            </div>
            </div>
        </div>
    </div>
  )
}

export default OurTraner
